<template>
  <div id="about" class="about">
    <div class="container">
      <div class="row">
        <div class="story col-md-7">
          <h2 class="title">WHO AM I</h2>
          <p class="text">嗨，我叫朱天胜，来自安徽芜湖，2017年毕业于西安科技大学，毕业后一直从事前端开发的工作。前端的技术可谓百花齐放，js不仅可用来写前端，也可在后端中发挥奇效，从JQUERY到VUE，从WEBPACK到NODE，前端技术发展迅猛，一刻也不能停下学习的脚步，徜徉在前端的海洋中也使我受益无穷。</p>
          <button class="cv btn btn-primary">MY CV</button>
        </div>
        <div class="skill col-md-5">
          <div class="skill-item js">
            <p class="text">HTML/JAVASCRIPT/CSS</p>
            <div class="hr"></div>
            <div class="percent">80%</div>
          </div>
          <div class="skill-item vue">
            <p class="text">JQUERY/BOOTSTRAP/VUE</p>
            <div class="hr"></div>
            <div class="percent">70%</div>
          </div>
          <div class="skill-item git">
            <p class="text">GIT/WEBPACK/ES6</p>
            <div class="hr"></div>
            <div class="percent">65%</div>
          </div>
          <div class="skill-item py">
            <p class="text">NGINX/NODE/PYTHON</p>
            <div class="hr"></div>
            <div class="percent">50%</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  mounted: function () {
    this.init()
  },
  methods: {
    init: function () {
      $('.js .hr').width($('.skill').width() * 0.8)
      $('.git .hr').width($('.skill').width() * 0.65)
      $('.vue .hr').width($('.skill').width() * 0.7)
      $('.py .hr').width($('.skill').width() * 0.5)
    }
  }
}
</script>

<style lang="less" scoped>
@import "../common/css/fadeIn.css";
.about{
  position: relative;
  width: 100%;
  padding: 50px 0;
  background: white;
  overflow-y: auto;
  z-index: 2;
  .story{
    .title{
      padding: 40px 0 40px 0;
      font-weight: bold;
    }
    .text{
      line-height: 50px;
      font-size: 18px;
    }
    .cv{
      font-size: 20px;
      margin-top: 40px;
    }
  }
  .skill{
    .skill-item{
      position: relative;
      margin: 50px 0;
      opacity: 0;
      .text{
        margin: 0;
        font-size: 20px;
        font-weight: bold;
      }
      .hr{
        display: inline-block;
        height: 5px;
        background: aqua;
        border-radius: 5px;
      }
      .percent{
        position: absolute;
        top: 12px;
        display: inline-block;
        width: 50px;
        height: 50px;
        line-height: 50px;
        text-align: center;
        font-weight: bold;
        border-radius: 50%;
        background: white;
        box-shadow: 5px 5px 5px #ddd;
      }
    }
    .skill-item:nth-child(2) { animation-delay: 0.3s; }
    .skill-item:nth-child(3) { animation-delay: 0.6s; }
    .skill-item:nth-child(4) { animation-delay: 0.9s; }
  }
}
</style>



