<template>
  <div class="header">
    <nav class="navbar navbar-default" role="navigation" style="z-index: 100;">
      <div class="container-fluid">
        <div class="navbar-header">
          <button type="button" class="navbar-toggle" data-toggle="collapse"
                  data-target="#example-navbar-collapse">
            <span class="sr-only">切换导航</span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </button>
          <a class="navbar-brand" href="#">个人网站</a>
        </div>
        <div class="collapse navbar-collapse" id="example-navbar-collapse">
          <ul class="nav navbar-nav">
            <!--<li :class="{active: }"><a href="#">home</a></li>-->
            <!--<li><a href="#">about</a></li>-->
            <!--<li><a href="#">service</a></li>-->
            <!--<li><a href="#">experience</a></li>-->
            <!--<li><a href="#">interest</a></li>-->
            <!--<li><a href="#">contact</a></li>-->
            <li v-for="(i, x) in nav" :key="x" :class="flag === x ? 'active' : ''">
              <a href="#">{{i}}</a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  props: [
    'index'
  ],
  data () {
    return {
      nav: ['home', 'about', 'service', 'experience', 'interest', 'contact'],
      flag: 0
    }
  },
  mounted: function () {
    this.init()
  },
  methods: {
    init: function () {
      // var _this = this
      $('a').on('click', function (e) {
        $('body,html').animate({scrollTop: $(`.${e.target.innerText}`).offset().top}, 500)
        if ($(this).text() === 'service') {
          $('body,html').animate({scrollTop: $(`.${e.target.innerText}`).offset().top + 50}, 500)
        }
        // _this.flag = 1
        // 点击添加active
        // $(this).addClass('active')
        // console.log($(this).siblings().parents())
        // $(this).parents().siblings().removeClass('active')
      })
    }
  },
  watch: {
    index: function (newVal) {
      this.flag = newVal
    }
  }
}
</script>

<style lang="less" scoped>
.header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  height: 52px;
  .navbar-default{
    background: #000 !important;
    border: none;
    li.active{
      a{
        background: #000 !important;
        color: white;
      }
    }
  }
}
</style>



