<template>
  <div class="home">
    <canvas class="canvas" ref="canvas"></canvas>
    <img src="../image/me.jpg" class="image">
    <p class="text"></p>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  mounted () {
    var myCanvas = this.$refs.canvas
    var ctx = myCanvas.getContext('2d')
    // 设置canvas宽高，此时设置高度会有滚动条出现
    myCanvas.width = $(window).width()
    myCanvas.height = $(window).height()
    // 小球类
    function Ball (x, y) {
      // 圆心坐标
      this.x = x
      this.y = y
      // 半径
      this.r = 40
      // 颜色
      this.color = 'rgba(' + parseInt(Math.random() * 256) + ',' + parseInt(Math.random() * 256) + ',' + parseInt(Math.random() * 256) + ', 0.8)'
      // 变化值
      this.dx = parseInt(Math.random() * 18) - 9
      this.dy = parseInt(Math.random() * 18) - 9
      ballsArr.push(this)
    }
    // 更新方法
    Ball.prototype.update = function () {
      this.x += this.dx
      this.y += this.dy
      this.r--
      // 当小球半径小于0时从数组中删除
      if (this.r < 0) {
        this.godie()
      }
    }
    // 渲染方法
    Ball.prototype.render = function () {
      ctx.beginPath()
      ctx.arc(this.x, this.y, this.r, 0, Math.PI * 2, true)
      ctx.closePath()
      ctx.fillStyle = this.color
      ctx.fill()
    }
    Ball.prototype.godie = function () {
      for (var i = 0; i < ballsArr.length; i++) {
        if (ballsArr[i] === this) {
          ballsArr.splice(i, 1)
        }
      }
    }
    var ballsArr = []
    myCanvas.onmousemove = function (event) {
      // console.log(event.clientX)
      /* eslint-disable no-new */
      for (let i = 0; i < 10; i++) {
        new Ball(event.clientX, event.clientY)
      }
    }
    setInterval(function () {
      ctx.clearRect(0, 0, myCanvas.width, myCanvas.height)
      for (var i = 0; i < ballsArr.length; i++) {
        ballsArr[i].update()
        ballsArr[i] && ballsArr[i].render()
      }
    }, 20)
    this.type()
  },
  methods: {
    type: function () {
      let str1 = 'THIS IS ZTS'
      let str2 = 'FROM WuHu'
      let index = 1
      let i = 0
      setInterval(() => {
        let str
        index === 1 ? str = str1 : str = str2
        if (i <= str.length) {
          $('.text').text(str.slice(0, i))
          console.log(str.slice(0, i))
          i++
        }
        if (i === str.length + 1) {
          i = 0
          index === 1 ? index = 2 : index = 1
        }
      }, 200)
    }
  }
}
</script>

<style lang="less" scoped>
.home{
  position: relative;
  text-align: center;
  z-index: 2;
  .canvas{
    position: relative;
  }
  .image{
    position: absolute;
    top: 200px;
    width: 150px;
    height: 150px;
    transform: translateX(-50%);
    border-radius: 50%;
    border: 10px solid white;
  }
  .text{
    position: absolute;
    top: 370px;
    width: 100%;
    font-size: 30px;
    text-align: center;
    color: white;
  }
}
</style>



