<template>
  <div id="app">
    <app-header ref="header" :index="index"></app-header>
    <app-home ref="home"></app-home>
    <app-about ref="about"></app-about>
    <app-service ref="service"></app-service>
    <app-experience ref="experience"></app-experience>
    <app-interest ref="interest"></app-interest>
    <app-contact ref="contact"></app-contact>
    <app-footer ref="footer"></app-footer>
  </div>
</template>

<script>
import $ from 'jquery'
import appHeader from './components/header'
import appHome from './components/home'
import appAbout from './components/about'
import appService from './components/service'
import appExperience from './components/experience'
import appInterest from './components/interest'
import appContact from './components/contact'
import appFooter from './components/footer'

export default {
  data () {
    return {
      // 离页面顶部的距离
      scrollTop: 0,
      heightArr: [],
      index: -1 // 当前active的tab
    }
  },
  mounted () {
    this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop + 1
    this.scrollTop < (this.$refs.home.$el.clientHeight + 100) ? this.$refs.footer.$el.style.display = 'none' : this.$refs.footer.$el.style.display = 'block'
    window.addEventListener('scroll', this.handleScroll)
    // this.heightArr.push(this.$refs.home.$el.clientHeight)
    // this.heightArr.push(this.$refs.home.$el.clientHeight + this.$refs.about.$el.clientHeight)
    let arr = [this.$refs.home.$el.clientHeight, this.$refs.about.$el.clientHeight, this.$refs.service.$el.clientHeight, this.$refs.experience.$el.clientHeight, this.$refs.interest.$el.clientHeight, this.$refs.contact.$el.clientHeight, this.$refs.footer.$el.clientHeight]
    for (let i = 0; i < arr.length; i++) {
      let length = 0
      for (let j = 0; j < i; j++) {
        length += arr[j]
      }
      this.heightArr.push(length)
    }
    this.handleTab()
    // console.log(this.heightArr)
  },
  methods: {
    handleScroll: function () {
      this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      this.scrollTop < (this.$refs.home.$el.clientHeight + 100) ? this.$refs.footer.$el.style.display = 'none' : this.$refs.footer.$el.style.display = 'block'
      this.handleTab()
      // console.log(this.scrollTop)
    },
    handleTab: function () {
      for (let i = 0; i < this.heightArr.length; i++) {
        if (this.heightArr[i + 1] && this.scrollTop >= this.heightArr[i] && this.scrollTop < this.heightArr[i + 1]) {
          this.index = i
          if (i === 1) {
            $(this.$refs.about.$el).find('.skill-item').addClass('animate')
          }
          if (i === 2) {
            $(this.$refs.service.$el).find('.item').addClass('animate')
          }
          if (i === 3) {
            console.log($(this.$refs.experience.$el).find('.item'))
            $(this.$refs.experience.$el).find('.item').addClass('animate')
          }
        }
      }
    }
  },
  components: {
    appHeader,
    appHome,
    appAbout,
    appService,
    appExperience,
    appInterest,
    appContact,
    appFooter
  }
}
</script>

<style lang="less" scoped>
#app{
  position: relative;
  overflow-x: hidden;
  background: #000;
}
</style>



